.seccionResumeCuenta {
  display: flex;
  flex-direction: column;
}
.seccionResumeCuenta .buscador {
  font-size: 2rem;
  font-weight: bold;
}
.seccionResumeCuenta .informacionCliente {
  display: flex;
  justify-content: space-between;
}
.seccionResumeCuenta .seccionTabla {
  min-height: 25rem;
}
.seccionResumeCuenta .seccionTabla table {
  width: 100%;
  border-collapse: collapse;
}
.seccionResumeCuenta .seccionTabla table .tbody {
  text-align: center;
}
.seccionResumeCuenta .saldoActual {
  text-align: end;
  font-weight: bold;
}
.seccionResumeCuenta header {
  margin-top: 2rem;
  padding: 2rem 2rem 0 2rem;
  background-color: white;
}
.seccionResumeCuenta main {
  padding: 0 2rem 0 2rem;
  background-color: white;
}
.seccionResumeCuenta .mostrarNegro {
  background-color: black;
}